/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import foto from "../../../assets/foto3.jpg";
import { Button } from "semantic-ui-react";
import "./InfoLateral.scss";

export function InfoLateral() {
  const Sidebar = { photo: foto, name: "Marco Pedemonte" };
  return (
    <div className="sidebar">
      <img src={Sidebar.photo} alt="Foto" />
      <h3>{Sidebar.name}</h3>
      <div className="social-media">
        <div className="github">
          <Button
            size="huge"
            as="a"
            target="_blank"
            href={"https://github.com/mpedemonte"}
            color={"black"}
            icon={"github"}
            className="custom-button"
          ></Button>
          <a href="https://github.com/mpedemonte" target="_blank">
            Github
          </a>
        </div>
        <div className="instagram">
          <Button
            size="huge"
            as="a"
            target="_blank"
            href={"https://www.instagram.com/mpedemonte94/"}
            color={"black"}
            icon={"instagram"}
            className="custom-button"
          ></Button>
          <a href="https://www.instagram.com/mpedemonte94/" target="_blank">
            Instagram
          </a>
        </div>
        <div className="linkedin">
          <Button
            size="huge"
            as="a"
            target="_blank"
            href={"https://www.linkedin.com/in/mpedemontesoto/"}
            color={"black"}
            icon={"linkedin"}
            className="custom-button"
          ></Button>
          <a href="https://www.linkedin.com/in/mpedemontesoto/" target="_blank">
            LinkedIn
          </a>
        </div>
      </div>
    </div>
  );
}
