import React, { useState, useEffect } from "react";
import { Servicio } from "../../../api";
import { size } from "lodash";
import { Loader } from "semantic-ui-react";
import { Item } from "semantic-ui-react";
import { ENV } from "../../../utils";
import "./ShowServicio.scss";

const servicioController = new Servicio();

export function ShowServicio() {
  const [servicios, setServicios] = useState(null);
  const [items, setItems] = useState(null);

  useEffect(() => {
    (async () => {
      try {
        setServicios(null);
        const response = await servicioController.getServicios(true);

        setItems(
          response.map((datos) => ({
            childkey: datos.order - 1,
            image: `${ENV.BASE_PATH}/${datos.miniature}`,
            header: datos.title,
            description: datos.description,
          }))
        );

        setServicios(response);
      } catch (error) {
        console.error(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!servicios) return <Loader active inline="centered" />;
  if (size(servicios) === 0) return "No hay ningun servicio";

  return (
    <div className="showServicio-info">
      <div className="showServicio-info-titulo">
        <h3 className="titulo-servicio"> SERVICIOS</h3>
      </div>
      <div className="servicios-items">
        <Item.Group items={items} />
      </div>
    </div>
  );
}
