/* eslint-disable jsx-a11y/iframe-has-title */
import React from "react";
import "./SetCv.scss";
import { Button, Icon } from "semantic-ui-react";
import { BasicModal } from "../../Shared";

import { ENV } from "../../../utils";
import { CurriculumForm } from "./CurriculumForm";

export function SetCv(props) {
  const { cv, onReload, onOpenCloseModal, showModal } = props;

  return (
    <>
      <div>
        <div>
          <Button
            className="cv-page__update"
            color="orange"
            basic
            onClick={onOpenCloseModal}
          >
            <Icon name="pencil" />
            Modificar CV
          </Button>
          <iframe
            className="pdf-viewer-container"
            src={`${ENV.BASE_PATH}/${cv.pdfcv}`}
          />
        </div>
      </div>
      <BasicModal
        show={showModal}
        close={onOpenCloseModal}
        title="Modificar CV"
      >
        <CurriculumForm
          onClose={onOpenCloseModal}
          onReload={onReload}
          cv={cv}
        />
      </BasicModal>
    </>
  );
}
