import { ENV } from "../utils";

export class Curriculum {
  baseApi = ENV.BASE_API;

  async getCv() {
    try {
      const url = `${this.baseApi}/${ENV.API_ROUTES.CURRICULUM}`;

      const response = await fetch(url);
      const result = await response.json();
      console.log(result);
      if (response.status !== 200) throw result;
      return result;
    } catch (error) {
      throw error;
    }
  }

  async updateCv(accessToken, idCv, data) {
    try {
      const formData = new FormData();
      Object.keys(data).forEach((key) => {
        formData.append(key, data[key]);
      });

      if (data.file) {
        formData.delete("pdfcv");
        formData.delete("file");
        formData.append("pdfcv", data.file);
      }

      const url = `${this.baseApi}/${ENV.API_ROUTES.CURRICULUM}/${idCv}`;
      const params = {
        method: "PATCH",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        body: formData,
      };

      const response = await fetch(url, params);
      const result = await response.json();

      if (response.status !== 200) throw result;
      return result;
    } catch (error) {
      throw error;
    }
  }
}
