import React from "react";
import { Routes, Route } from "react-router-dom";
import { ClientLayoutHome } from "../layouts";
import {
  Home,
  Contacto,
  Curriculum,
  Portafolio,
  Servicios,
  PostPortafolio,
  PostServicios,
} from "../pages/web";

export function WebRouter() {
  const loadLayout = (Layout, Page) => {
    return (
      <Layout>
        <Page />
      </Layout>
    );
  };
  return (
    <Routes>
          <Route path="/" element={loadLayout(ClientLayoutHome, Home)} />
      <Route
        path="/portafolio"
        element={loadLayout(ClientLayoutHome, Portafolio)}
      />
      <Route
        path="/portafolio/:path"
        element={loadLayout(ClientLayoutHome, PostPortafolio)}
      />
      <Route
        path="/servicios"
        element={loadLayout(ClientLayoutHome, Servicios)}
      />
      <Route
        path="/servicios/:path"
        element={loadLayout(ClientLayoutHome, PostServicios)}
      />
      <Route
        path="/curriculum"
        element={loadLayout(ClientLayoutHome, Curriculum)}
      />
      <Route
        path="/contacto"
        element={loadLayout(ClientLayoutHome, Contacto)}
      />
  
    </Routes>
  );
}
