import React, { useState, useEffect } from "react";
import { Container, Button, Grid, Image } from "semantic-ui-react";
import { Carousel as Cart } from "react-carousel-minimal";
import { Link } from "react-router-dom";
import { ENV } from "../../../utils";

import { Portafolio, Servicio } from "../../../api";
import "./Carrousel.scss";

const portafolioController = new Portafolio();
const servicioController = new Servicio();

export function Carrousel(props) {
  const { active } = props;
  const [imagenes, setImagenes] = useState(null);
  const [servicio_data, setServicio_data] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(0);

  //const [portafolios, setPortafolios] = useState(null);
  //const [servicios, setServicios] = useState(null);

  const params = {
    limit: 5,
    active: active,
  };

  useEffect(() => {
    (async () => {
      try {
        const response = await portafolioController.getPortafolios(params);

        const response2 = await servicioController.getServicios(active);
        //setServicios(response2);
        const data = response2.map((datos) => ({
          image: `${ENV.BASE_PATH}/${datos.miniature}`,
        }));
        setServicio_data(data);

        //setPortafolios(response.docs);
        const images = response.docs.map((data) => ({
          image: `${ENV.BASE_PATH}/${data.miniature}`,
        }));
        setImagenes(images);
      } catch (error) {
        console.error(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % servicio_data.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [servicio_data]);

  return (
    <Container>
      <div className="contenido">
        <div className="carousel-container">
          {imagenes ? (
            <>
              <h2 className="carousel-title">Trabajos realizados</h2>

              <Cart
                data={imagenes}
                time={5000}
                width="100%"
                height="100%"
                radius="10px"
                slideNumber={false}
                slideBackgroundColor="transparent"
                captionPosition="bottom"
                automatic={true}
                dots={true}
                pauseIconColor="white"
                pauseIconSize="0px"
                slideImageFit="cover"
                thumbnails={false}
                thumbnailWidth="100px"
                showArrows={false}
                style={{
                  textAlign: "center",
                }}
              />
              <div className="redirect-button">
                <Button primary as={Link} to="/portafolio">
                  Ver portafolio
                </Button>
              </div>
            </>
          ) : (
            <span></span>
          )}
        </div>
        <div className="grid-container">
          {servicio_data ? (
            <>
              <h2 className="grid-title">Servicios</h2>
              <Grid columns="one">
                <Grid.Row>
                  <Grid.Column>
                    {servicio_data && (
                      <Image
                        className={`grid-image ${
                          currentIndex !== 0 ? "fade-in" : ""
                        }`}
                        src={servicio_data[currentIndex].image}
                      />
                    )}
                  </Grid.Column>
                </Grid.Row>
              </Grid>
              <div className="redirect-button">
                <Button primary as={Link} to="/servicios">
                  Ver servicios
                </Button>
              </div>
            </>
          ) : (
            <span></span>
          )}
        </div>
      </div>
    </Container>
  );
}
