import * as Yup from "yup";

export function initialValues(servicio) {
  return {
    title: servicio?.title || "",
    miniature: servicio?.miniature || "",
    file: null,
    content: servicio?.content || "",
    path: servicio?.path || "",
    order: servicio?.order || 0,
    active: servicio?.active || true,
    description: servicio?.description || "",
  };
}

export function validationSchema() {
  return Yup.object({
    title: Yup.string().required(true),
    content: Yup.string().required(true),
    miniature: Yup.string().required(true),
    path: Yup.string().required(true),
    order: Yup.number().required(true),
    description: Yup.string().required(true),
  });
}
