import { ENV } from "../utils";

export class Portafolio {
  baseApi = ENV.BASE_API;
  async getPortafolios(params) {
    try {
      const pageFilter = `page=${params?.page || 1}`;
      const limitFilter = `limit=${params?.limit || 10}`;
      const activeFilter = `active=${params?.active}`;
      const url = `${this.baseApi}/${ENV.API_ROUTES.PORTAFOLIO}?${pageFilter}&${limitFilter}&${activeFilter}`;
      const response = await fetch(url);

      const result = await response.json();

      if (response.status !== 200) throw result;
      return result;
    } catch (error) {
      throw error;
    }
  }

  async createPortafolio(accessToken, data) {
    try {
      const formData = new FormData();
      Object.keys(data).forEach((key) => {
        formData.append(key, data[key]);
      });

      if (data.fileMiniature) {
        formData.delete("miniature");
        formData.delete("fileMiniature");
        formData.append("miniature", data.fileMiniature);
      }
      if (data.fileImagen1) {
        formData.delete("imagen1");
        formData.delete("fileImagen1");
        formData.append("imagen1", data.fileImagen1);
      }
      if (data.fileImagen2) {
        formData.delete("imagen2");
        formData.delete("fileImagen2");
        formData.append("imagen2", data.fileImagen2);
      }
      if (data.fileImagen3) {
        formData.delete("imagen3");
        formData.delete("fileImagen3");
        formData.append("imagen3", data.fileImagen3);
      }
      if (data.fileImagen4) {
        formData.delete("imagen4");
        formData.delete("fileImagen4");
        formData.append("imagen4", data.fileImagen4);
      }
      if (data.fileImagen5) {
        formData.delete("imagen5");
        formData.delete("fileImagen5");
        formData.append("imagen5", data.fileImagen5);
      }

      const url = `${this.baseApi}/${ENV.API_ROUTES.PORTAFOLIO}`;
      const params = {
        method: "POST",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        body: formData,
      };

      const response = await fetch(url, params);
      const result = await response.json();

      if (response.status !== 201) throw result;

      return result;
    } catch (error) {
      throw error;
    }
  }

  async updatePortafolio(accessToken, idPortafolio, data) {
    try {
      const formData = new FormData();
      Object.keys(data).forEach((key) => {
        formData.append(key, data[key]);
      });

      if (data.fileMiniature) {
        formData.delete("miniature");
        formData.delete("fileMiniature");
        formData.append("miniature", data.fileMiniature);
      }
      if (data.fileImagen1) {
        formData.delete("imagen1");
        formData.delete("fileImagen1");
        formData.append("imagen1", data.fileImagen1);
      }
      if (data.fileImagen2) {
        formData.delete("imagen2");
        formData.delete("fileImagen2");
        formData.append("imagen2", data.fileImagen2);
      }
      if (data.fileImagen3) {
        formData.delete("imagen3");
        formData.delete("fileImagen3");
        formData.append("imagen3", data.fileImagen3);
      }
      if (data.fileImagen4) {
        formData.delete("imagen4");
        formData.delete("fileImagen4");
        formData.append("imagen4", data.fileImagen4);
      }
      if (data.fileImagen5) {
        formData.delete("imagen5");
        formData.delete("fileImagen5");
        formData.append("imagen5", data.fileImagen5);
      }
      const url = `${this.baseApi}/${ENV.API_ROUTES.PORTAFOLIO}/${idPortafolio}`;
      const params = {
        method: "PATCH",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        body: formData,
      };

      const response = await fetch(url, params);
      const result = await response.json();

      if (response.status !== 200) throw result;

      return result;
    } catch (error) {
      throw error;
    }
  }

  async deletePortafolio(accessToken, idPortafolio) {
    try {
      const url = `${this.baseApi}/${ENV.API_ROUTES.PORTAFOLIO}/${idPortafolio}`;
      const params = {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };
      const response = await fetch(url, params);
      const result = await response.json();

      if (response.status !== 200) throw result;
      return result;
    } catch (error) {
      throw error;
    }
  }

  async getPortafolio(path) {
    try {
      const url = `${this.baseApi}/${ENV.API_ROUTES.PORTAFOLIO}/${path}`;

      const response = await fetch(url);
      const result = await response.json();

      if (response.status !== 200) throw result;

      return result;
    } catch (error) {
      throw error;
    }
  }
}
