import React, { useState, useEffect } from "react";
import { size, map } from "lodash";
import { Loader, Pagination } from "semantic-ui-react";
import { Portafolio } from "../../../../api";
import { PortafolioItem } from "../PortafolioItem";
import "./ListPortafolio.scss";

const portafolioController = new Portafolio();

export function ListPortafolio(props) {
  const { reload, onReload, active } = props;
  const [portafolios, setPortafolios] = useState(null);
  const [pagination, setPagination] = useState(null);
  const [page, setPage] = useState(1);

  const params = {
    page: page,
    limit: 5,
    active: active,
  };

  useEffect(() => {
    (async () => {
      try {
        setPortafolios(null);
        const response = await portafolioController.getPortafolios(params);

        setPortafolios(response.docs);

        setPagination({
          limit: response.limit,
          page: response.page,
          pages: response.pages,
          total: response.total,
        });
      } catch (error) {
        console.error(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page, reload, active]);

  const changePage = (_, data) => {
    setPage(data.activePage);
  };

  if (!portafolios) return <Loader active inline="centered" />;
  if (size(portafolios) === 0) return "No hay ningun portafolio";

  return (
    <div className="list-post">
      {map(portafolios, (portafolios) => (
        <PortafolioItem
          key={portafolios._id}
          portafolios={portafolios}
          onReload={onReload}
        />
      ))}

      <div className="portafolios__pagination">
        {
          <Pagination
            totalPages={pagination.pages}
            defaultActivePage={pagination.page}
            ellipsisItem={false}
            firstItem={false}
            lastItem={false}
            onPageChange={changePage}
          />
        }
      </div>
    </div>
  );
}
