import React, { useState } from "react";
import { Button, Icon, Confirm, Item } from "semantic-ui-react";
import { BasicModal } from "../../../Shared";
import { ENV } from "../../../../utils";
import { useAuth } from "../../../../hooks";
import { Servicio } from "../../../../api";
import "./ServicioItem.scss";
import { ServicioForm } from "../ServicioForm";

const servicioController = new Servicio();

export function ServicioItem(props) {
  const { servicios, onReload } = props;
  const { accessToken } = useAuth();

  const [showModal, setShowModal] = useState(false);
  const [titleModal, setTitleModal] = useState("");

  const [showConfirm, setShowConfirm] = useState(false);
  const [confirmMessage, setConfirmMessage] = useState("");
  const [isDelete, setIsDelete] = useState(false);

  const onOpenCloseModal = () => setShowModal((prevState) => !prevState);
  const onOpenCloseConfirm = () => setShowConfirm((prevState) => !prevState);

  const openUpdateServicio = () => {
    setTitleModal(`Actualizar servicio: ${servicios.title}`);
    onOpenCloseModal();
  };
  const openDesactivateActivateConfirm = () => {
    setIsDelete(false);
    setConfirmMessage(
      servicios.active
        ? `Desactivar el servicio ${servicios.title}`
        : `Activar el servicio ${servicios.title}`
    );
    onOpenCloseConfirm();
  };

  const onActivateDesactivate = async () => {
    try {
      console.log({
        active: !servicios.active,
      });
      await servicioController.updateServicio(accessToken, servicios._id, {
        active: !servicios.active,
      });
      onReload();
      onOpenCloseConfirm();
    } catch (error) {
      console.error(error);
    }
  };

  const openDeleteConfirm = () => {
    setIsDelete(true);
    setConfirmMessage(`Elminar el servicio ${servicios.title}`);
    onOpenCloseConfirm();
  };

  const onDelete = async () => {
    try {
      await servicioController.deleteServicio(accessToken, servicios._id);
      onReload();
      onOpenCloseConfirm();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <div className="servicio-item">
        <Item.Group relaxed>
          <Item>
            <div className="order">{servicios.order}</div>

            <Item.Image
              size="small"
              src={`${ENV.BASE_PATH}/${servicios.miniature}`}
            />

            <Item.Content verticalAlign="middle">
              <Item.Header>{servicios.title}</Item.Header>
              <Item.Description>{servicios.description}</Item.Description>
            </Item.Content>
            <Item.Extra>
              <Button
                icon
                color="red"
                floated="right"
                onClick={openDeleteConfirm}
              >
                <Icon name={"trash"} />
              </Button>
              <Button
                icon
                color={servicios.active ? "orange" : "teal"}
                floated="right"
                onClick={openDesactivateActivateConfirm}
              >
                <Icon name={servicios.active ? "ban" : "check"} />
              </Button>
              <Button icon primary floated="right" onClick={openUpdateServicio}>
                <Icon name={"pencil"} />
              </Button>
            </Item.Extra>
          </Item>
        </Item.Group>
      </div>

      <BasicModal show={showModal} close={onOpenCloseModal} title={titleModal}>
        <ServicioForm
          onClose={onOpenCloseModal}
          onReload={onReload}
          servicio={servicios}
        />
      </BasicModal>
      <Confirm
        open={showConfirm}
        onCancel={onOpenCloseConfirm}
        onConfirm={isDelete ? onDelete : onActivateDesactivate}
        content={confirmMessage}
        size="mini"
      />
    </>
  );
}
