import React, { useState, useEffect } from "react";
import { size, map } from "lodash";
import { Loader } from "semantic-ui-react";
import { Servicio } from "../../../../api";
import { ServicioItem } from "../ServicioItem";

const servicioController = new Servicio();
export function ListServicio(props) {
  const { reload, onReload, active } = props;
  const [servicios, setServicios] = useState(null);

  useEffect(() => {
    (async () => {
      try {
        setServicios(null);
        const response = await servicioController.getServicios(active);

        setServicios(response);
      } catch (error) {
        console.error(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reload, active]);

  if (!servicios) return <Loader active inline="centered" />;
  if (size(servicios) === 0) return "No hay ningun servicio";
  return (
    <div>
      {map(servicios, (servicios) => (
        <ServicioItem
          key={servicios._id}
          servicios={servicios}
          onReload={onReload}
        />
      ))}
    </div>
  );
}
