import React from "react";
import { useFormik } from "formik";
import { initialValues, validationSchema } from "./PortafolioForm.form";
import { Form, Image } from "semantic-ui-react";
import { useDropzone } from "react-dropzone";
import { Portafolio } from "../../../../api";
import { useAuth } from "../../../../hooks";
import { Editor } from "@tinymce/tinymce-react";
import { ENV } from "../../../../utils";
import "./PortafolioForm.scss";

const portafolioController = new Portafolio();

export function PortafolioForm(props) {
  const { onClose, onReload, portafolio } = props;
  const { accessToken } = useAuth();

  const formik = useFormik({
    initialValues: initialValues(portafolio),
    validationSchema: validationSchema(),
    validateOnChange: false,
    onSubmit: async (formValue) => {
      try {
        if (!portafolio) {
          await portafolioController.createPortafolio(accessToken, formValue);
        } else {
          await portafolioController.updatePortafolio(
            accessToken,
            portafolio._id,
            formValue
          );
        }
        onReload();
        onClose();
      } catch (error) {
        console.error(error);
      }
    },
  });

  const {
    getRootProps: getRootMiniatureProps,
    getInputProps: getInputMiniatureProps,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileMiniature = acceptedFile[0];
      formik.setFieldValue("miniature", URL.createObjectURL(fileMiniature));
      formik.setFieldValue("fileMiniature", fileMiniature);
    },
  });

  const {
    getRootProps: getRootImagen1Props,
    getInputProps: getInputImagen1Props,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileImagen1 = acceptedFile[0];
      formik.setFieldValue("imagen1", URL.createObjectURL(fileImagen1));
      formik.setFieldValue("fileImagen1", fileImagen1);
    },
  });

  const {
    getRootProps: getRootImagen2Props,
    getInputProps: getInputImagen2Props,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileImagen2 = acceptedFile[0];
      formik.setFieldValue("imagen2", URL.createObjectURL(fileImagen2));
      formik.setFieldValue("fileImagen2", fileImagen2);
    },
  });

  const {
    getRootProps: getRootImagen3Props,
    getInputProps: getInputImagen3Props,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileImagen3 = acceptedFile[0];
      formik.setFieldValue("imagen3", URL.createObjectURL(fileImagen3));
      formik.setFieldValue("fileImagen3", fileImagen3);
    },
  });

  const {
    getRootProps: getRootImagen4Props,
    getInputProps: getInputImagen4Props,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileImagen4 = acceptedFile[0];
      formik.setFieldValue("imagen4", URL.createObjectURL(fileImagen4));
      formik.setFieldValue("fileImagen4", fileImagen4);
    },
  });

  const {
    getRootProps: getRootImagen5Props,
    getInputProps: getInputImagen5Props,
  } = useDropzone({
    accept: "image/jpeg, image/png, image/jpg",
    onDrop: (acceptedFile) => {
      const fileImagen5 = acceptedFile[0];
      formik.setFieldValue("imagen5", URL.createObjectURL(fileImagen5));
      formik.setFieldValue("fileImagen5", fileImagen5);
    },
  });

  const getMiniature = () => {
    if (formik.values.fileMiniature) {
      return formik.values.miniature;
    } else if (formik.values.miniature) {
      return `${ENV.BASE_PATH}/${formik.values.miniature}`;
    }
    return null;
  };

  const getImagen1 = () => {
    if (formik.values.fileImagen1) {
      return formik.values.imagen1;
    } else if (formik.values.imagen1) {
      return `${ENV.BASE_PATH}/${formik.values.imagen1}`;
    }
    return null;
  };

  const getImagen2 = () => {
    if (formik.values.fileImagen2) {
      return formik.values.imagen2;
    } else if (formik.values.imagen2) {
      return `${ENV.BASE_PATH}/${formik.values.imagen2}`;
    }
    return null;
  };

  const getImagen3 = () => {
    if (formik.values.fileImagen3) {
      return formik.values.imagen3;
    } else if (formik.values.imagen3) {
      return `${ENV.BASE_PATH}/${formik.values.imagen3}`;
    }
    return null;
  };

  const getImagen4 = () => {
    if (formik.values.fileImagen4) {
      return formik.values.imagen4;
    } else if (formik.values.imagen4) {
      return `${ENV.BASE_PATH}/${formik.values.imagen4}`;
    }
    return null;
  };

  const getImagen5 = () => {
    if (formik.values.fileImagen5) {
      return formik.values.imagen5;
    } else if (formik.values.imagen5) {
      return `${ENV.BASE_PATH}/${formik.values.imagen5}`;
    }
    return null;
  };

  return (
    <Form onSubmit={formik.handleSubmit}>
      <div className="portafolio-form__miniature" {...getRootMiniatureProps()}>
        <input {...getInputMiniatureProps()} />
        {getMiniature() ? (
          <Image size="small" src={getMiniature()} />
        ) : (
          <div>
            <span>Arrastra tu Miniatura</span>
          </div>
        )}
      </div>

      <Form.Input
        name="title"
        placeholder="Titulo del trabajo"
        onChange={formik.handleChange}
        value={formik.values.title}
        error={formik.errors.title}
      />
      <Form.TextArea
        name="description"
        placeholder="Descripcion"
        onChange={formik.handleChange}
        value={formik.values.description}
        error={formik.errors.description}
      />

      <Form.Group widths="equal">
        <Form.Input
          name="order"
          type="number"
          placeholder="order"
          onChange={formik.handleChange}
          value={formik.values.order}
          error={formik.errors.order}
        />
        <Form.Input
          name="path"
          placeholder="Path del trabajo"
          onChange={formik.handleChange}
          value={formik.values.path}
          error={formik.errors.path}
        />
      </Form.Group>

      <div className="portafolio-form__images">
        <div className="portafolio-form__imagen" {...getRootImagen1Props()}>
          <input {...getInputImagen1Props()} />
          {getImagen1() ? (
            <Image size="small" src={getImagen1()} />
          ) : (
            <div>
              <span>Arrastra tu 1</span>
            </div>
          )}
        </div>
        <div className="portafolio-form__imagen" {...getRootImagen2Props()}>
          <input {...getInputImagen2Props()} />
          {getImagen2() ? (
            <Image size="small" src={getImagen2()} />
          ) : (
            <div>
              <span>Arrastra tu 2</span>
            </div>
          )}
        </div>
        <div className="portafolio-form__imagen" {...getRootImagen3Props()}>
          <input {...getInputImagen3Props()} />
          {getImagen3() ? (
            <Image size="small" src={getImagen3()} />
          ) : (
            <div>
              <span>Arrastra tu 3</span>
            </div>
          )}
        </div>
        <div className="portafolio-form__imagen" {...getRootImagen4Props()}>
          <input {...getInputImagen4Props()} />
          {getImagen4() ? (
            <Image size="small" src={getImagen4()} />
          ) : (
            <div>
              <span>Arrastra tu 4</span>
            </div>
          )}
        </div>
        <div className="portafolio-form__imagen" {...getRootImagen5Props()}>
          <input {...getInputImagen5Props()} />
          {getImagen5() ? (
            <Image size="small" src={getImagen5()} />
          ) : (
            <div>
              <span>Arrastra tu 5</span>
            </div>
          )}
        </div>
      </div>

      <Editor
        init={{
          height: 400,
          menubar: true,
          plugins: [
            "advlist autolink lists link image charmap print preview anchor",
            "searchreplace visualblocks code fullscreen",
            "insertdatetime media table paste code help wordcount",
          ],
          toolbar:
            // eslint-disable-next-line no-multi-str
            "undo redo | formatselect | bold italic backcolor | \
             alignleft aligncenter alignright alignjustify | \
             bullist numlist outdent indent | removeformat | help",
        }}
        initialValue={formik.values.content}
        onBlur={(e) => formik.setFieldValue("content", e.target.getContent())}
      />

      <Form.Button type="submit" primary fluid loading={formik.isSubmitting}>
        {portafolio ? "Actualizar Trabajo" : "Crear Trabajo"}
      </Form.Button>
    </Form>
  );
}
