import React from "react";
import { Menu, Icon, MenuItem } from "semantic-ui-react";
import { Link, useLocation } from "react-router-dom";
import "./AdminMenu.scss";
export function AdminMenu() {
  const { pathname } = useLocation();

  const isCurrentPath = (path) => {
    if (path === pathname) return true;
    return false;
  };

  return (
    <Menu fluid vertical icon text className="admin-menu">
      <MenuItem
        as={Link}
        to="/admin/portafolio"
        active={isCurrentPath("/admin/portafolio")}
      >
        <Icon name="book" />
        Portafolios
      </MenuItem>

      <MenuItem
        as={Link}
        to="/admin/menu"
        active={isCurrentPath("/admin/menu")}
      >
        <Icon name="bars" />
        Menus
      </MenuItem>

      <MenuItem
        as={Link}
        to="/admin/servicios"
        active={isCurrentPath("/admin/servicios")}
      >
        <Icon name="briefcase" />
        Servicios
      </MenuItem>

      <MenuItem
        as={Link}
        to="/admin/curriculum"
        active={isCurrentPath("/admin/curriculum")}
      >
        <Icon name="newspaper outline" />
        Curriculum
      </MenuItem>
    </Menu>
  );
}
