import React from "react";
import { Routes, Route } from "react-router-dom";
import { Auth, Portafolio, Curriculum, Menu, Servicio } from "../pages/admin";
import { AdminLayout } from "../layouts";
import { useAuth } from "../hooks";

export function AdminRouter() {
  const { user } = useAuth();

  const loadLayout = (Layout, Page) => {
    return (
      <Layout>
        <Page />
      </Layout>
    );
  };
  return (
    <Routes>
      {!user ? (
        <Route path="/admin/*" element={<Auth />} />
      ) : (
        <>
          {["/admin", "admin/portafolio"].map((path) => (
            <Route
              key={path}
              path={path}
              element={loadLayout(AdminLayout, Portafolio)}
            />
          ))}
          <Route
            path="/admin/servicios"
            element={loadLayout(AdminLayout, Servicio)}
          />
          <Route path="/admin/menu" element={loadLayout(AdminLayout, Menu)} />
          <Route
            path="/admin/curriculum"
            element={loadLayout(AdminLayout, Curriculum)}
          />
        </>
      )}
    </Routes>
  );
}
