import React, { useState } from "react";
import { Tab, Button, Icon } from "semantic-ui-react";
import { BasicModal } from "../../../components/Shared";
import "./Servicio.scss";
import { ListServicio, ServicioForm } from "../../../components/Admin/Servicio";

export function Servicio() {
  const [showModal, setShowModal] = useState(false);
  const [reload, setReload] = useState(false);

  const onOpenCloseModal = () => setShowModal((prevState) => !prevState);
  const onReload = () => setReload((prevState) => !prevState);

  const panes = [
    {
      menuItem: "Servicios Activos",
      render: () => (
        <Tab.Pane attached={false}>
          <ListServicio reload={reload} onReload={onReload} active={true} />
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Servicios Inactivos",
      render: () => (
        <Tab.Pane attached={false}>
          <ListServicio reload={reload} onReload={onReload} active={false} />
        </Tab.Pane>
      ),
    },
  ];
  return (
    <>
      <div className="servicio-page">
        <Button
          className="servicio-page__add"
          primary
          basic
          onClick={onOpenCloseModal}
        >
          <Icon name="plus circle" />
          Nuevo Servicio
        </Button>
        <Tab
          menu={{
            color: "blue",
            secondary: true,
          }}
          panes={panes}
        />
      </div>
      <BasicModal
        show={showModal}
        close={onOpenCloseModal}
        title="Crear Servicio"
      >
        <ServicioForm onClose={onOpenCloseModal} onReload={onReload} />
      </BasicModal>
    </>
  );
}
