import React, { useState } from "react";
import { Tab, Button, Icon } from "semantic-ui-react";
import { BasicModal } from "../../../components/Shared";
import "./Portafolio.scss";
import {
  ListPortafolio,
  PortafolioForm,
} from "../../../components/Admin/Portafolio";

export function Portafolio() {
  const [showModal, setShowModal] = useState(false);
  const [reload, setReload] = useState(false);

  const onOpenCloseModal = () => setShowModal((prevState) => !prevState);
  const onReload = () => setReload((prevState) => !prevState);

  const panes = [
    {
      menuItem: "Trabajos Activos",
      render: () => (
        <Tab.Pane attached={false}>
          <ListPortafolio reload={reload} onReload={onReload} active={true} />
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Trabajos Inactivos",
      render: () => (
        <Tab.Pane attached={false}>
          <ListPortafolio reload={reload} onReload={onReload} active={false} />
        </Tab.Pane>
      ),
    },
  ];

  return (
    <>
      <div className="portafolio-page">
        <Button
          className="portafolio-page__add"
          primary
          basic
          onClick={onOpenCloseModal}
        >
          <Icon name="plus circle" />
          Nuevo Trabajo
        </Button>
        <Tab
          menu={{
            color: "blue",
            secondary: true,
          }}
          panes={panes}
        />
      </div>
      <BasicModal
        show={showModal}
        close={onOpenCloseModal}
        title="Crear Trabajo"
      >
        <PortafolioForm onClose={onOpenCloseModal} onReload={onReload} />
      </BasicModal>
    </>
  );
}
