import * as Yup from "yup";

export function initialValues(portafolio) {
  return {
    title: portafolio?.title || "",
    miniature: portafolio?.miniature || "",
    fileMiniature: null,
    fileImagen1: null,
    fileImagen2: null,
    fileImagen3: null,
    fileImagen4: null,
    fileImagen5: null,
    content: portafolio?.content || "",
    path: portafolio?.path || "",
    imagen1: portafolio?.imagen1 || "",
    imagen2: portafolio?.imagen2 || "",
    imagen3: portafolio?.imagen3 || "",
    imagen4: portafolio?.imagen4 || "",
    imagen5: portafolio?.imagen5 || "",
    active: portafolio?.active || true,
    order: portafolio?.order || 0,
    description: portafolio?.description || "",
  };
}

export function validationSchema() {
  return Yup.object({
    title: Yup.string().required(true),
    content: Yup.string().required(true),
    miniature: Yup.string().required(true),
    imagen1: Yup.string(),
    imagen2: Yup.string(),
    imagen3: Yup.string(),
    imagen4: Yup.string(),
    imagen5: Yup.string(),
    path: Yup.string().required(true),
    order: Yup.number().required(true),
    description: Yup.string().required(true),
  });
}
