import React, { useState, useEffect } from "react";
import { Curriculum as cvAPI } from "../../../api";
import { size } from "lodash";
import { Loader } from "semantic-ui-react";
import { ENV } from "../../../utils";
import "./CurriculumVitae.scss";

const cvController = new cvAPI();

export function CurriculumVitae() {
  const [cv, setCv] = useState(null);
  useEffect(() => {
    (async () => {
      try {
        setCv(null);
        const response = await cvController.getCv();
        setCv(response);
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);
  if (!cv) return <Loader active inline="centered" />;
  if (size(cv) === 0) return "No hay ningun menu";

  return (
    <div className="curriculumvitae-info">
      <div className="curriculumvitae-info-titulo">
        <h3 className="titulo-curriculumvitae">CURRICULUM</h3>
      </div>
      <div className="curriculumvitae-info__curriculum">
        <iframe
          id="cv"
          title="Curriculum Vitae"
          className="pdf-viewer-container"
          src={`${ENV.BASE_PATH}/${cv.pdfcv}`}
        />
      </div>
    </div>
  );
}
