import React, { useState, useEffect } from "react";
import { Curriculum } from "../../../api";
import { size } from "lodash";
import { Loader } from "semantic-ui-react";
import { SetCv } from "./SetCv";

const cvController = new Curriculum();

export function GetCv(props) {
  const { reload, onReload, onOpenCloseModal, showModal } = props;
  const [cv, setCv] = useState(null);

  useEffect(() => {
    (async () => {
      try {
        setCv(null);
        const response = await cvController.getCv();
        setCv(response);
      } catch (error) {
        console.error(error);
      }
    })();
  }, [reload]);

  if (!cv) return <Loader active inline="centered" />;
  if (size(cv) === 0) return "No hay ningun menu";

  return (
    <div>
      <SetCv
        cv={cv}
        onReload={onReload}
        onOpenCloseModal={onOpenCloseModal}
        showModal={showModal}
      />
    </div>
  );
}
