import React, { useState, useEffect } from "react";
import logo from "../../../assets/mp6.png";
import { Menu, Button } from "semantic-ui-react";
import { Menu as MenuApi } from "../../../api";
import { map } from "lodash";
import "./TopBar.scss";
import { Link } from "react-router-dom";

const menuController = new MenuApi();

export function TopBar() {
  const [menu, setMenu] = useState(null);
  const [activeItem, setActiveItem] = useState("");

  const [isMenuOpen, setIsMenuOpen] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        const response = await menuController.getMenu(true);
        setMenu(response);
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  const handleItemClick = (name) => {
    setActiveItem(name);
  };

  useEffect(() => {
    // Obtener la ruta actual
    const currentPath = window.location.pathname;

    if (currentPath === "/") {
      setActiveItem("Inicio");
    } else {
      const pth = currentPath.split("/");
      setActiveItem(pth[1].charAt(0).toUpperCase() + pth[1].slice(1));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [menu, window.location.pathname]);

  const handleBurgerMenu = () => {
    const menu = document.querySelector(".top");
    if (menu.style.display === "block") {
      menu.style.display = "none";
      //setIsBurgerMenuActive(false); // Si el menú está visible, ocúltalo
      setIsMenuOpen(!isMenuOpen);
    } else {
      menu.style.display = "block";
      //setIsBurgerMenuActive(true);
      setIsMenuOpen(!isMenuOpen); // Si el menú está oculto, muéstralo
    }
  };

  useEffect(() => {
    const handleWindowSizeChange = () => {
      const menu = document.querySelector(".top");

      if (!window.matchMedia("(max-width: 1200px)").matches) {
        if (menu.style.display === "block") {
          menu.style.display = "none";
          setIsMenuOpen(false);
        }
      }
    };

    window.addEventListener("resize", handleWindowSizeChange);

    // Llama a la función una vez al cargar la página
    handleWindowSizeChange();

    // Limpia el event listener cuando el componente se desmonta
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  return (
    <div className="top-bar">
      <div className="top-bar__left">
        <Link to={`/`} onClick={() => handleItemClick("Inicio")}>
          <img src={logo} alt="logo" className="logo" />
        </Link>
      </div>
      <div className="top-bar__right">
        <div className="top-bar-menu">
          <Menu className="menu" secondary pointing inverted>
          {map(menu, (item) => (
             
             <Link to={item.title === "Inicio" ? `/` : `/${item.title}`}>
             <Menu.Item
             key={item._id}
             name={`${item.title}`}
             active={activeItem === `${item.title}`}
             onClick={() => handleItemClick(item.title)}
             //href={item.path}
           /></Link>
              
            ))}
          </Menu>
        </div>
      </div>

      <div className="mostrar">
        <div className="top">
          <Menu secondary inverted vertical>
            {map(menu, (item) => (
             <Link to={item.title === "Inicio" ? `/` : `/${item.title}`}>
             <Menu.Item
             key={item._id}
             name={`${item.title}`}
             active={activeItem === `${item.title}`}
             onClick={() => handleItemClick(item.title)}
             //href={item.path}
           /></Link>
              
            ))}
          </Menu>
        </div>
        <div className="redes-topbar">
          <Button
            size="big"
            as="a"
            target="_blank"
            href={"https://github.com/mpedemonte"}
            color={"black"}
            icon={"github"}
            className="custom-button"
          ></Button>

          <Button
            size="big"
            as="a"
            target="_blank"
            href={"https://www.instagram.com/mpedemonte94/"}
            color={"pink"}
            icon={"instagram"}
            className="custom-button"
          ></Button>

          <Button
            size="big"
            as="a"
            target="_blank"
            href={"https://www.linkedin.com/in/mpedemontesoto/"}
            color={"linkedin"}
            icon={"linkedin"}
            className="custom-button"
          ></Button>
        </div>
        <button
          className={`burgerMenu ${isMenuOpen ? "active" : ""}`}
          onClick={handleBurgerMenu}
        >
          <span className="burgerLine"></span>
          <span className="burgerLine"></span>
          <span className="burgerLine"></span>
        </button>
      </div>
    </div>
  );
}
