import React, { useState, useEffect } from "react";
import { ENV } from "../../../utils";
import { map } from "lodash";
import { DateTime } from "luxon";
import { Link } from "react-router-dom";
import { Portafolio } from "../../../api";
import { Card, Image, Button } from "semantic-ui-react";
import "./ShowPortafolio.scss";

const portafolioController = new Portafolio();
export function ShowPortafolio() {
  const [portafolios, setPortafolios] = useState(null);
  const [pagination, setPagination] = useState(null);
  const [page, setPage] = useState(1);

  const isCurrentLastPage = pagination?.page >= pagination?.pages;

  const params = {
    page: page,
    limit: 4,
    active: true,
  };
  useEffect(() => {
    (async () => {
      try {
        const response = await portafolioController.getPortafolios(params);
        setPagination({
          page: response.page,
          pages: response.pages,
        });
        if (!portafolios) setPortafolios(response.docs);
        else setPortafolios([...portafolios, ...response.docs]);
      } catch (error) {
        console.error(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page]);

  const loadMore = () => {
    setPage((prevState) => prevState + 1);
  };

  return (
    <div className="showPortafolio-info">
      <div className="showPortafolio-info-titulo">
        <h3 className="titulo-portafolio">PORTAFOLIO</h3>
      </div>
      <div className="portafolios">
        {map(portafolios, (portafolio) => (
          <div key={portafolio._id} className="portafolios__item">
            <Card>
              <Card.Content header={portafolio.title} />
              <Image
                src={`${ENV.BASE_PATH}/${portafolio.miniature}`}
                wrapped
                ui={false}
              />
              <Card.Content>
                <Card.Description>{portafolio.description}</Card.Description>
                <Card.Meta className="card-meta">
                  {DateTime.fromISO(portafolio.created_at)
                    .setLocale("es")
                    .toFormat("dd 'de' LLLL 'del' yyyy")}
                </Card.Meta>
              </Card.Content>
              <Card.Content extra>
                <div className="button-extra">
                  <Link
                    className="list-post-item"
                    to={`/portafolio/${portafolio.path}`}
                  >
                    <Button basic primary>
                      Mas información
                    </Button>
                  </Link>
                </div>
              </Card.Content>
            </Card>
          </div>
        ))}
      </div>
      {!isCurrentLastPage && (
        <div className="more">
          <Button primary onClick={loadMore}>
            Cargar mas...
          </Button>
        </div>
      )}
    </div>
  );
}
