import React, { useRef } from "react";
import { useFormik } from "formik";
import { initialValues, validationSchema } from "./ContactoForm.form";
import { Form } from "semantic-ui-react";
import ReCAPTCHA from "react-google-recaptcha";
import { RECAPTCHA_KEYS } from "../../../utils";
import "./ContactoForm.scss";
import { Contacto as Contact } from "../../../api/contacto";
import Swal from "sweetalert2";

const contactController = new Contact();

export function ContactoForm() {
  const captcha = useRef(null);
  const onChange = () => {
    if (captcha.current.getValue()) {
      formik.setFieldValue("captcha", true);
    } else {
      formik.setFieldValue("captcha", false);
    }
  };

  const formik = useFormik({
    initialValues: initialValues(),
    validationSchema: validationSchema(),
    validateOnChange: false,
    onSubmit: async (formValue) => {
      try {
        delete formValue["captcha"];
        await contactController.sendEmail(formValue);
        Enviado();
        formik.resetForm();
        captcha.current.reset();
      } catch (error) {
        console.error(error);
      }
    },
  });

  const Enviado = () => {
    Swal.fire({
      position: "center",
      icon: "success",
      title: "El Mensaje fue enviado exitosamente",
      showConfirmButton: false,
      timer: 3000,
    });
  };

  return (
    <div className="contactoform">
      <div className="contactoform-title">
        <h3 className="contactoform-title__titulo">CONTACTO</h3>
      </div>
      <div className="contactoform-form">
        <Form onSubmit={formik.handleSubmit}>
          <Form.Input
            name="name"
            placeholder="Nombre"
            label="Nombre *"
            onChange={formik.handleChange}
            value={formik.values.name}
            error={formik.errors.name}
          />
          <Form.Input
            name="email"
            placeholder="Correo electrónico"
            label="Correo electrónico *"
            onChange={formik.handleChange}
            value={formik.values.email}
            error={formik.errors.email}
          />
          <Form.Input
            name="phone"
            placeholder="Teléfono"
            label="Teléfono"
            onChange={formik.handleChange}
            value={formik.values.phone}
            error={formik.errors.phone}
          />
          <Form.TextArea
            name="mensaje"
            placeholder="Mensaje"
            label="Mensaje *"
            onChange={formik.handleChange}
            value={formik.values.mensaje}
            error={formik.errors.mensaje}
          />
          <div className="recaptcha">
            <p className="error-captcha">{formik.errors.captcha ?? " "}</p>
            <ReCAPTCHA
              ref={captcha}
              name="captcha"
              sitekey={`${RECAPTCHA_KEYS.SITE_KEY}`}
              onChange={onChange}
            />
          </div>
          <Form.Button
            type="submit"
            primary
            fluid
            loading={formik.isSubmitting}
          >
            Enviar
          </Form.Button>
        </Form>
      </div>
    </div>
  );
}
