/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback } from "react";
import { useFormik } from "formik";
import { initialValues, validationSchema } from "./CurriculumForm.form";
import { Form } from "semantic-ui-react";
import { Curriculum } from "../../../api";
import { useDropzone } from "react-dropzone";
import { useAuth } from "../../../hooks";

const cvController = new Curriculum();

export function CurriculumForm(props) {
  const { onClose, onReload, cv } = props;
  const { accessToken } = useAuth();

  const formik = useFormik({
    initialValues: initialValues(cv),
    validationSchema: validationSchema(),
    validateOnChange: false,
    onSubmit: async (formValue) => {
      try {
        const response = await cvController.updateCv(
          accessToken,
          cv._id,
          formValue
        );
        console.log(response);
        onReload();
        onClose();
      } catch (error) {
        console.error(error);
      }
    },
  });

  const onDrop = useCallback((acceptedFiles) => {
    const file = acceptedFiles[0];
    formik.setFieldValue("pdfcv", URL.createObjectURL(file));
    formik.setFieldValue("file", file);
  });

  const { getRootProps, getInputProps } = useDropzone({
    accept: "image/*,.pdf",
    onDrop,
  });

  const getcurriculum = () => {
    if (formik.values.file) {
      return formik.values.pdfcv;
    } else if (formik.values.pdfcv) {
      return `${formik.values.pdfcv}`;
    }
    return null;
  };

  return (
    <Form className="curriculum-form" onSubmit={formik.handleSubmit}>
      <div className="curriculum-form__pdf" {...getRootProps()}>
        <input {...getInputProps()} />
        {getcurriculum() ? (
          <p>{getcurriculum()}</p>
        ) : (
          <div>
            <span>Arrastra tu Cv</span>
          </div>
        )}
      </div>
      <Form.Button
        type="submit"
        primary
        basic
        fluid
        loading={formik.isSubmitting}
      >
        Actualizar CV
      </Form.Button>
    </Form>
  );
}
