import React, { useState } from "react";
import { Button, Icon, Confirm, Item } from "semantic-ui-react";
import { BasicModal } from "../../../Shared";
import { ENV } from "../../../../utils";
import { useAuth } from "../../../../hooks";
import { Portafolio } from "../../../../api";
import "./PortafolioItem.scss";
import { PortafolioForm } from "../PortafolioForm";

const portafolioController = new Portafolio();

export function PortafolioItem(props) {
  const { portafolios, onReload } = props;
  const { accessToken } = useAuth();

  const [showModal, setShowModal] = useState(false);
  const [titleModal, setTitleModal] = useState("");

  const [showConfirm, setShowConfirm] = useState(false);
  const [confirmMessage, setConfirmMessage] = useState("");
  const [isDelete, setIsDelete] = useState(false);

  const onOpenCloseModal = () => setShowModal((prevState) => !prevState);
  const onOpenCloseConfirm = () => setShowConfirm((prevState) => !prevState);

  const openUpdatePortafolio = () => {
    setTitleModal(`Actualizar trabajo: ${portafolios.title}`);
    onOpenCloseModal();
  };

  const openDesactivateActivateConfirm = () => {
    setIsDelete(false);
    setConfirmMessage(
      portafolios.active
        ? `Desactivar el portafolio ${portafolios.title}`
        : `Activar el portafolio ${portafolios.title}`
    );
    onOpenCloseConfirm();
  };

  const onActivateDesactivate = async () => {
    try {
      await portafolioController.updatePortafolio(
        accessToken,
        portafolios._id,
        {
          active: !portafolios.active,
        }
      );
      onReload();
      onOpenCloseConfirm();
    } catch (error) {
      console.error(error);
    }
  };

  const openDeleteConfirm = () => {
    setIsDelete(true);
    setConfirmMessage(`Elminar el portafolio ${portafolios.title}`);
    onOpenCloseConfirm();
  };

  const onDelete = async () => {
    try {
      await portafolioController.deletePortafolio(accessToken, portafolios._id);
      onReload();
      onOpenCloseConfirm();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <div className="portafolio-item">
        <Item.Group relaxed>
          <Item>
            <div className="order">{portafolios.order}</div>

            <Item.Image
              size="small"
              src={`${ENV.BASE_PATH}/${portafolios.miniature}`}
            />

            <Item.Content verticalAlign="middle">
              <Item.Header>{portafolios.title}</Item.Header>
              <Item.Description>{portafolios.description}</Item.Description>
            </Item.Content>
            <Item.Extra>
              <Button
                icon
                color="red"
                floated="right"
                onClick={openDeleteConfirm}
              >
                <Icon name={"trash"} />
              </Button>
              <Button
                icon
                color={portafolios.active ? "orange" : "teal"}
                floated="right"
                onClick={openDesactivateActivateConfirm}
              >
                <Icon name={portafolios.active ? "ban" : "check"} />
              </Button>
              <Button
                icon
                primary
                floated="right"
                onClick={openUpdatePortafolio}
              >
                <Icon name={"pencil"} />
              </Button>
            </Item.Extra>
          </Item>
        </Item.Group>
      </div>

      <BasicModal show={showModal} close={onOpenCloseModal} title={titleModal}>
        <PortafolioForm
          onClose={onOpenCloseModal}
          onReload={onReload}
          portafolio={portafolios}
        />
      </BasicModal>

      <Confirm
        open={showConfirm}
        onCancel={onOpenCloseConfirm}
        onConfirm={isDelete ? onDelete : onActivateDesactivate}
        content={confirmMessage}
        size="mini"
      />
    </>
  );
}
